body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --mainBlue: rgb(12, 51, 182);
  --lightBlue: rgb(92, 84, 202);
  --mainWhite: #ffffff;
  --mainDark: rgb(109, 113, 131);
  --mainYellow: rgb(0, 225, 255);
  --mainAccent: pink;
}

body {
  /* font-family: 'Montserrat', sans-serif !important; */
  color: rgb(109, 113, 131);
  color: var(--mainDark);
  background: #ffffff !important;
  background: var(--mainWhite) !important;
}

.text-title {
  /* font-family: 'Space Mono', monospace !important; */
}

.text-blue {
  color: rgb(109, 113, 131);
  color: var(--mainDark)
}
.text-bright {
  color: rgb(109, 113, 131) !important;
  color: var(--mainDark) !important;
}

.br-primary {
  background: rgb(109, 113, 131);
  background: var(--mainDark);
}


.xth_random0 {
  position: relative;
  display: flex;
  /* width: 120px; */
  height: 80px;
  min-width: 60px;
  /* padding-left: 16px; */
  align-items: center;
  flex: 0 1 auto;
}

.x_randomize_0 {
  position: relative;
  display: flex;
  overflow: hidden;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}


.x_randomize {
  position: absolute;
  display: flex;
  width: 240px;
  height: 240px;
  justify-content: center;
  align-items: center;
  border-style: solid;
  border-width: 0px;
  border-color: #fff;
  border-radius: 100px;
  background-image: radial-gradient(circle farthest-corner at 100% 100%, #0ff 31%, #ff00d4 37%, #04f 60%, #ff8c00 63%, #fbff00 79%);
  box-shadow: inset -5px -5px 8px -4px #06f, inset 1px 1px 3px 0 rgba(243, 0, 37, 0.27), 0 0 9px -2px #0bf;
  transition: all 575ms cubic-bezier(.175, .885, .32, 1.275);
  font-family: Materialicons, sans-serif;
  color: #fff;
  font-size: 24px;
  cursor: pointer;
}

.x_randomize:hover {
  -webkit-transform: none;
          transform: none;
}

.x_randomize.x_randomize--rotated {
  -webkit-transform: rotate(140deg);
          transform: rotate(140deg);
}

.x_randomize.x_randomize--rotated:hover {
  -webkit-transform: rotate(279deg);
          transform: rotate(279deg);
}


.xicon_white {
  display: flex;
  width: 20px;
  height: 20px;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  background-color: #fff;
  color: rgba(255, 0, 162, 0.82);
  font-size: 18px;
}
